import logo from './logo.svg'
import './App.css'
import React from 'react'

class Timer extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      date: new Date(),
    }
  }
  componentDidMount(){
    this.timerID = setInterval( () =>this.tick(),500)
  }
  componentWillUnmount(){
    clearInterval(this.timerID)
  }
  tick(){
    this.setState({
      date:new Date()
    })
  }
  render(){
    const tz = this.props.timeZone ? this.props.timeZone : 'Asia/Shanghai'
    return (
      <div>
        <span>
          current time of {tz} is{' '}
          {this.state.date.toLocaleTimeString('en-US', { timeZone: tz })}
        </span>
        <br />
      </div>
    )
  }
}

class TimerGroup extends React.Component{
  render(){
    var timers = []
    timers.push(<Timer/>)
    for(var i of this.props.tzs){
      timers.push(<Timer timeZone={i}/>)
    }
    return timers
  }
}

class App extends React.Component {
  constructor(props){
    super(props)
    this.name = 'timer app'
    this.state = {name:''}
    this.handleClick = this.handleClick.bind(this)
  }
  handleClick(){
    console.log('clicked')
    this.setState({name:this.name})
  }
  render(){
    
    return (
      <div className='App'>
        <h1> {this.state.name} </h1>
        <h1>Hello {this.props.user ? this.props.user : 'stranger'}</h1>
        <TimerGroup tzs={['America/Chicago']}/>
        <button onClick={this.handleClick}>click me</button>
      </div>
    )
  }
  
}

export default App
